<template>
    <div class="task py-3">
        <template v-if="task">
            <div class="task-item" style="color: #3e3e3e">
                <h2 class="mb-5">Контроль задачи «{{task.vacancyName}}»</h2>
                <el-steps :active="2" simple finish-status="success" class="mb-5 task-steps blue-box">
                    <el-step title="Старт"      icon="fas fa-fast-forward"></el-step>
                    <el-step title="Договор"    icon="fas fa-handshake"></el-step>
                    <el-step title="Работа"    icon="fas fa-stopwatch"></el-step>
                    <el-step title="Завершение" icon="fas fa-check-double"></el-step>
                </el-steps>
                <div class="row mb-5">
                    <div class="col-md-5 h-100">
                        <div class="task-process blue-box mb-4 d-flex justify-content-between">
                            <el-progress type="circle" :status="status" :strokeWidth="20" :percentage="percentage" :width="160"></el-progress>
                            <div class="task-info px-3" style="font-size: 1.4rem">
                                <h3 class="mb-3">Процесс</h3>
                                <p>
                                    <span style="font-size: .7em; opacity: .7">Дата начала: </span>
                                    <span>{{task.startDate | moment('D.MM.YY')}}</span>
                                </p>
                                <p>
                                    <span style="font-size: .7em; opacity: .7">Дедлайн: </span>
                                    <span>{{task.deadline | moment('D.MM.YY')}}</span>
                                </p>
                                <p class="mt-4" style="font-size: 1.2em; line-height: 1.2">
                                    <span style="font-size: .7em; opacity: .7">Оплата: </span><br>
                                    <span>{{task.cost}} <span style="opacity: .7; font-size: .7em">₸</span></span>
                                </p>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-7 h-100">
                        <div class="blue-box py-2">
                            <div class="task-info">
                                <h3 class="px-5 mt-2 mb-1">Участники договора</h3>
                                <div class="row px-5 py-3">
                                    <div class="col-3 pt-3 pr-2">
                                        <p class="task-header text-right" style="opacity: .6">Заказчик:</p>
                                    </div>
                                    <div class="col-9 pl-2">
                                        <div class="d-flex px-3">
                                            <div class="round-image mr-3" :style="{backgroundImage: `url(${task.companyImage})`}"></div>
                                            <h4 class="mt-3">{{task.companyName}}</h4>
                                        </div>
                                    </div>
                                </div>
                                <hr style="border-top: 1px solid rgba(182, 187, 193, 0.18);">
                                <div class="row px-5 py-3">
                                    <div class="col-3 pt-3 pr-2">
                                        <p class="task-header text-right" style="opacity: .6">Исполнитель:</p>
                                    </div>
                                    <div class="col-9 pl-2">
                                        <div class="d-flex  px-3">
                                            <div class="round-image mr-3" :style="{backgroundImage: `url(${task.studentImage})`}"></div>
                                            <h4 class="mt-3">{{task.freelancerId}}</h4>
                                            <div class="ml-auto">
                                                <el-tooltip content="Написать" placement="left">
                                                    <el-button @click="chatVisible = true" icon="el-icon-message" type="warning" circle plain size="mini" class="mt-2 mr-2"></el-button>
                                                </el-tooltip>
                                                <chat-send-message :receiverTitle="task.companyName"
                                                                   :chatVisible.sync="chatVisible"
                                                                   :receiverId="task.freelancerId"></chat-send-message>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="blue-box px-5 py-5 task-description mb-5">
                    <h3 class="mb-4">Описание задачи</h3>
                    <p v-html="task.description"></p>
                </div>
                <div class="d-flex justify-content-end py-3">
                    <el-button type="warning"   icon="far fa-clock" plain> <span class="ml-2">Продлить задачу</span></el-button>
                    <el-button type="danger"    icon="far fa-times-circle" plain><span class="ml-2">Отменить задачу</span></el-button>
                    <el-button type="success"   icon="fas fa-check-double" plain><span class="ml-2">Завершить задачу</span></el-button>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
    import {
        GET_ONGOING_TASKS,
        GET_SELECTED_TASK,
        SET_SELECTED_TASK, TASKS
    } from "../../store/mutation-types";
    import ChatSendMessage from "../Chat/ChatSendMessage";
    export default {
        components: {ChatSendMessage},
        name: 'company-task',
        data() {
            return {
                chatVisible: false
            }
        },
        computed: {
            task() {
                return this.$store.getters[TASKS + GET_SELECTED_TASK]
            },
            percentage() {
                const today = (new Date()).getTime()
                const endDate = this.task.deadline.getTime()
                const startDate = this.task.startDate.getTime()
                return (today > endDate) ? 100 : parseInt((today - startDate) / (endDate - startDate) * 100)
            },
            status() {
                const endDate = this.task.deadline.getTime()
                const today = (new Date()).getTime()
                return (today > endDate) ? 'exception' : ''
            }
        },
        methods: {
            selectTask(id) {
                this.$store.commit(TASKS + SET_SELECTED_TASK, id)
                if (!this.task) {
                    this.$store.dispatch(TASKS + GET_ONGOING_TASKS)
                }
            }
        },
        watch: {
            $route(to, from) {
                if (to.params.id !== from.params.id) {
                    this.selectTask(to.params.id)

                }
            }
        },
        mounted() {
            this.selectTask(this.$route.params.id)
        }
    }
</script>